.list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
}

.list-2 {
  &__item {
    &:not(:last-child) {
      margin-bottom: 17px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: -9px;
        border-bottom: 1px dashed $border-color;
        left: 0;
        right: 42px;
      }
    }
  }
}

.list-3 {
  margin-bottom: 26px;

  &__item {
    border-bottom: 1px dotted $border-color;
    padding-bottom: 24px;
    margin-bottom: 24px;
  }
}

.list-4 {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 12px;
  list-style: none;
  border-top: 1px solid $border-color;
  padding: 20px 0;
  margin: 50px 0 0;
}

.list-5 {
  &__item {
    &:not(:last-child) {
      margin-bottom: 36px;
    }
  }
}

.faq-list {
  list-style: none;
  padding-left: 15px;
  margin: 0;

  &__item {
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 4px;
      height: 4px;
      background-color: $primary;
      position: absolute;
      top: 9px;
      left: -13px;
    }
  }

  &__link {
    font-size: 14px;
    line-height: 25px;
    color: inherit;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap: 17px 8px;
  overflow: hidden;

  &__item {
    position: relative;

    &::before {
      content: "";
      display: block;
      width: calc(100% + 8px);
      border-top: 1px dashed $border-color;
      position: absolute;
      top: -9px;
      left: 0;
    }
  }
}
